<template>
  <div class="edit-container">
    <h2 @click="titleClick" v-if="!titleChange">{{qsItem.title}}</h2>
    <input type="text" name="qsTitle" v-if="titleChange"
    v-model="titleValue"
    @blur="onblur"
    @keyup.enter="onsubmit"
    ref="titleInput">
    <div class="content">
      <div class="questions" v-for="(qs, index) in qsItem.question">
        <div class="qs-left">
          <p class="qs-title">
            {{qs.num}}&nbsp;{{qs.title}}&nbsp;{{getMsg(qs)}}
          </p>
          <p v-for="option in qs.options" class="option">
            <label>
              <input 
              type="radio" 
              :name="`${qs.num}-${qs.title}`"
              v-if="qs.type === 'radio'">
              <input 
              type="checkbox" 
              :name="`${qs.num}-${qs.title}`"
              v-if="qs.type === 'checkbox'">{{option}}
            </label>
          </p>
          <textarea 
          v-if="qs.type === 'textarea'"></textarea>
        </div>
        <div class="qs-right">
          <label><input type="checkbox" :value="qs.isNeed" v-model="qs.isNeed">
          </label>
          <p>
            <span v-if="index !== 0" @click="goUp(index)"></span>
            <span v-if="index !== qsItem.question.length - 1" @click="goDown(index)"></span>
            <span @click="copy(index, qs)"></span>
            <span @click="del(index)"></span>
          </p>
        </div>
      </div>
      <div class="add">
        <transition name="slide">
          <div class="add-option" v-if="showBtn">
            <button @click="addRadio"></button>
            <button @click="addCheckbox"></button>
            <button @click="addTextarea"></button>
          </div>
        </transition>
        <div class="add-item" @click="addItemClick">
          <span class="add-icon" >+</span><span></span>
        </div>
      </div>
    </div>
    <div class="shadow" v-if="showAddQsDialog">
      <div class="add-qs-dialog">
        <header>
          <span></span>
          <span class="close-btn" @click="showAddQsDialog = false">X</span>
        </header>
        <p>{{info}}</p>
        <label><input type="text" v-model="qsInputTitle"></label>
        <label v-if="showAddOptionInput"><input type="text" v-model="qsInputOptions"></label>
        <div class="btn-box">
          <button class="yes" @click="validateAddQs"></button>
          <button @click="showAddQsDialog = false"></button>
        </div>
      </div>
    </div>
    <div class="shadow" v-if="showDialog">
      <div class="dialog">
        <header>
          <span></span>
          <span class="close-btn" @click="cancel">X</span>
        </header>
        <p>{{info}}</p>
        <div class="btn-box">
          <button class="yes" @click="iterator.next()"></button>
          <button @click="cancel"></button>
        </div>
      </div>
    </div>
    <footer>
      <span></span>
      <calendar-input 
      :showCalendar="false" 
      :limit="limit" 
      @getValue="getValue">
      </calendar-input>
      <div class="btn-box">
        <button class="save" @click="iterator = save(); iterator.next()"></button>
        <button class="issue" @click="iterator = issueQs(); iterator.next()"></button>
      </div>
    </footer>
  </div>
</template>

<script>
import storage from '../store.js'
import calendarInput from './calendar-input'
/**
 * A module that define qs-edit view
 * @exports qs-edit
 * @author oyh(Reusjs)
 */
export default {
  name: 'qsEdit',
  components: {
    calendarInput
  },
  data() {
    return {
      qsItem: {},
      qsList: storage.get(),
      isError: false,
      showBtn: false,
      titleChange: false,
      titleValue: '',
      showAddQsDialog: false,
      showAddOptionInput: true,
      qsInputTitle: '',
      qsInputOptions: [],
      info: '',
      selectTime: '',
      addOptionType: '',
      limit: {},
      showDialog: false,
      iterator: {},
      isGoIndex: false
    }
  },
  beforeRouteEnter(to, from ,next) {
    let num = to.params.num
    let theItem = {}
    if (num != 0) {
      let length = storage.get().length
      if (num < 0 || num > length) {
        alert('!')
        next('/')
      } else {
        for (let i = 0; i < length; i++) {
          if (storage.get()[i].num == num) {
            theItem = storage.get()[i]
            break
          }
        }
      }
      if (theItem.state === 'noissue') {
        next()
      } else {
        alert('')
        next('/')
      }
    } else {
      next()
    }
  },
  created() {
    this.fetchData()
  },
  methods: {
    fetchData() {
      this.limit = { 
          minYear: new Date().getFullYear(),
          minMonth: new Date().getMonth() + 1,
          minDay: new Date().getDate(),
          maxYear: 2030,
          maxMonth: 3,
          maxDay: 20
        }
      if (this.$route.params.num == 0) {
        let item = {}
        item.num        = this.qsList.length + 1
        item.title      = ''
        item.time       = ''
        item.state      = 'noissue'
        item.question   = []
        item.stateTitle = ''
        item.checked    = false
        this.qsItem = item
        this.qsList.push(this.qsItem)
      } else {
        let i = 0
        for (let length = this.qsList.length; i < length; i++) {
          if (this.qsList[i].num == this.$route.params.num) {
            this.qsItem = this.qsList[i]
            break
          }
        }
        if ( i === this.qsList.length) this.isError = true
      } 
    },
    getMsg(item) {
      let msg = ''
      if (item.type === 'radio') {
        msg = '()'
      } else if (item.type === 'checkbox') {
        msg = '()'
      } else {
        msg = '()'
      }

      return item.isNeed ? `${msg} *` : msg
    },
    onblur() {
      this.titleValue = this.titleValue.trim()
      this.qsItem.title = this.titleValue === '' ? this.qsItem.title : this.titleValue
      this.titleChange = false
    },
    onsubmit() {
      this.titleValue = this.titleValue.trim()
      this.qsItem.title = this.titleValue === '' ? this.qsItem.title : this.titleValue
      this.titleChange = false
    },
    titleClick() {
      this.titleChange = !this.titleChange
      setTimeout( () => {
        this.$refs.titleInput.focus()
      }, 150 )
    },
    swapItems(oldIndex, newIndex) {
      let [newVal] = this.qsItem.question.splice(newIndex, 1, this.qsItem.question[oldIndex])
      this.qsItem.question.splice(oldIndex, 1, newVal)
    },
    goUp(index) {
      this.swapItems(index, index - 1)
    },
    goDown(index) {
      this.swapItems(index, index + 1)
    },
    copy(index, qs) {
      if (this.questionLength === 10) return alert('')
      qs = Object.assign({}, qs)
      this.qsItem.question.splice(index, 0, qs)
    },
    del(index) {
      this.qsItem.question.splice(index, 1)
    },
    addItemClick() {
      if (this.showBtn === false) {
        this.questionLength === 10 ? alert('') : this.showBtn = !this.showBtn
      } else {
        this.showBtn = !this.showBtn
      }
    },
    showAddDialog(msg, showOption) {
      this.showAddQsDialog = true
      this.showAddOptionInput = showOption
      this.info = msg
      this.qsInputTitle = ''
      this.qsInputOptions = ''
    },
    addRadio() {
      if (this.questionLength === 10) return alert('')
      this.showAddDialog(', ","', true)
      this.addOptionType = 'radio'
    },
    addCheckbox() {
      if (this.questionLength === 10) return alert('')
      this.showAddDialog(', ","', true)
      this.addOptionType = 'checkbox'
    },
    addTextarea() {
      if (this.questionLength === 10) return alert('')
      this.showAddDialog('', false)
      this.addOptionType = 'textarea' 
    },
    validateAddQs() {
      let qsTitle = this.qsInputTitle.trim()
      if (qsTitle === '') return alert('')
      if (this.showAddOptionInput) {
        let qsOptions = this.qsInputOptions.trim()
        if (qsOptions === '') return alert('')
        qsOptions = qsOptions.split(',')
        for (let i = 0, length = qsOptions.length; i < length; i++) {
          if (qsOptions[i].trim() === '') {
            return alert('')
          }
        }
        this.qsItem.question.push({
          'num': this.qsItem.question.length - 1, 
          'title': qsTitle, 
          'type': this.addOptionType,
          'isNeed': true,
          'options':qsOptions
        })
        this.showAddQsDialog = false
      } else {
        this.qsItem.question.push({
          'num': this.qsItem.question.length - 1,
          'title': qsTitle,
          'type': 'textarea',
          'isNeed': true
        })
        this.showAddQsDialog = false
      }
    },
    getValue(selectTime) {
      this.selectTime = selectTime
      this.qsItem.time = this.selectTime
    },
    *save() {
      this.showDialog = true
      this.info = '?'
      yield
      if (this.qsItem.question.length === 0) {
        this.showDialog = false
        alert('')
      } else {
        storage.save(this.qsList)
        this.info = '?'
        this.isGoIndex = true
      }
      
      yield
      this.qsItem.state = 'inissue'
      this.qsItem.stateTitle = ''
      storage.save(this.qsList)
      this.showDialog = false
      this.$router.push({path:'/'})
    },
    *issueQs() {
      this.showDialog = true
      this.info = '?'
      yield
      if (this.qsItem.question.length === 0) {
        this.showDialog = false
        alert('')
      } else {
        this.qsItem.state = 'inissue'
        this.qsItem.stateTitle = ''
        storage.save(this.qsList)
        this.showDialog = false
        this.$router.push({path:'/'})
      }
      
    },
    cancel() {
      this.showDialog = false
      if (this.isGoIndex === true) {
        this.$router.push({path:'/'})
      }
    }
  },
  computed: {
    questionLength() {
      return this.qsItem.question.length
    }
  },
  watch: {
    '$route': 'fetchData',
    qsItem: {
      handler(newVal) {
        newVal.question.forEach( (item, index) => {
          item.num = `Q${index + 1}`
        } )
      },
      deep: true
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../style/QS-edit';
</style>


